@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&family=Trocchi&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div {
  transition: all ease-in-out 0.2s;
}

p,
button,
span,
a,
textarea,
label,
option,
select {
  font-family: "Poppins";
  color: #133540;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
  width: calc(100% - 3vw);
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 3vw;
}

.options-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header-item {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

#language-selection{
  margin-right: 30px;
}

.header-text {
  color: #fff;
  font-size: 18px;
  margin: 20px;
}

/* fill for chevron */
.MuiSelect-iconOutlined {
  fill: white !important;
}

#menu-button{
  color: white;
  display: none;
}

nav{
  display: flex;
  flex-direction: row;
}



@media screen and (max-width:600px){

  .header-container{
    position: fixed;
    top:0;
    height: 50px;
    padding: 8px 2%;
    width: 96%;
    margin: 0;

    box-shadow: 0 0 5px 8px rgba(71, 71, 71, 0.767);
  
    z-index: 2;
  }

  .fakenav{
    height: 64px;
  }

  nav{
    position: absolute;
    left: 0;
    top: 66px;
    flex-direction: column;
    background-color: white;
    width: 100%;
    border: solid 0 transparent;
    border-bottom-right-radius: 10px;
    box-shadow: 5px 5px 5px 3px rgba(71, 71, 71, 0.548);
    transition: all 0.5s ease-in-out;
  }
  nav[hidden]{
    display: none;
  }

  nav .header-item{
    width: 100%;
  }
  nav .header-text{
    color: black;
  }
  nav .header-item:last-child .header-text{
    color: red;
  }
  #menu-button{
    display: flex;
    font-size: 3em;
    margin-right: 4%;
  }
  #language-selection{
    margin-right: 2%;
  }
  .MuiSelect-outlined.MuiSelect-outlined{
    padding: 8% 10%;
  }
}



.column-logo {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
}

.logo-bar {
    height: 20%;
    border-radius: 100px;
}

.bar-1 { width: 100%; } 
.bar-2 { width: 65%; } 
.bar-3 { width: 35%; } 

.animated-logo-1 {
    -webkit-animation: logo1 infinite ease-in-out 0.6s;
            animation: logo1 infinite ease-in-out 0.6s;
}   
.animated-logo-2 {
    -webkit-animation: logo2 infinite ease-in-out 0.6s 0.1s;
            animation: logo2 infinite ease-in-out 0.6s 0.1s;
}   
.animated-logo-3 {
    -webkit-animation: logo3 infinite ease-in-out 0.6s 0.2s;
            animation: logo3 infinite ease-in-out 0.6s 0.2s;
}   

@-webkit-keyframes logo1 {
    30% { transform: translateX(20px); }
    70% { transform: translateX(0); }
}   

@keyframes logo1 {
    30% { transform: translateX(20px); }
    70% { transform: translateX(0); }
}

@-webkit-keyframes logo2 {
    30% { transform: translateX(12px); }
    70% { transform: translateX(0); }
}

@keyframes logo2 {
    30% { transform: translateX(12px); }
    70% { transform: translateX(0); }
}

@-webkit-keyframes logo3 {
    30% { transform: translateX(8px); }
    70% { transform: translateX(0); }
}

@keyframes logo3 {
    30% { transform: translateX(8px); }
    70% { transform: translateX(0); }
}

.home-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.home-wrapper {
    width: 100%;
    height: calc(100% - 10vh);
    padding-top: 4vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.column,.column#home-left-column {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.column#home-left-column {
    align-items: flex-start;
    justify-content: flex-start;
}

.post-image {
    width: 20vw;
    height: auto;
    margin: 6vh;
}

.linkedin-image {
    height: auto;
    width: 40vw;
    box-shadow: rgba(0, 0, 0, 0.3) 0 0 40px;
}

.main-text {
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    position: relative;
    align-self: flex-start;
}

.absolute-text {
    position: absolute;
    bottom: -16px;
    width: 30vw;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-start;
}

.important-text {
    margin: 0;
    font-weight: bold;
    font-size: 40px;
    text-align: end;
}

.second-text {
    font-family: "Trocchi" !important;
    color: rgba(0, 0, 0, 0.5);
    font-size: 20px;
    margin-top: 10px;
}

.cta-container {
    width: 180px;
    height: 40px;
    border-radius: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.cta-container:hover {
    opacity: 0.9;
}

.cta-text {
    font-size: 20px;
    color: white;
    font-weight: bold;
}

@media screen and (max-width:850px){
    .home-wrapper .important-text{font-size: 1.8em;}
    .home-wrapper .second-text{font-size: 1.1em;}

    .home-wrapper{
        padding-left: 5%;
        padding-right: 5%;
        width: 90%;
    }
}

@media screen and (max-width:600px){
    .home-wrapper{ flex-direction: column;}
    .home-wrapper .absolute-text{width: 60vw;}
    .home-wrapper .important-text{font-size: 2em;}
    .home-wrapper .second-text{ font-size: 1.5em;}
    #home-left-column{
        margin-left: 5%;
        width: 70%;
    }

    .post-image {
        width: 70vw;
    }
    
    .linkedin-image {
        display: none;
    }
}
.modal {
  display: "flex";
  align-items: "center";
  justify-content: "center";
}

.backdrop {
  background-color: #020a1a;
  opacity: 0.5;
}

.card {
  margin-left: 2vw;
  margin-right: 2vw;
  width: 80vh;
  border-radius: 20px;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.overflowable {
  overflow-y: auto;
  max-height: calc(80vh - 20px);
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  margin-bottom: 20px;
}

.close-icon {
  position: absolute;
  top: 2.5%;
  right: 2.5%;
  cursor: pointer;
}

#creation-modal-container{
  justify-content: center;
}

#creation-modal-toptext{
  font-size: 1.5em;
  margin-bottom: 6%;
  margin-top: 6%;
  padding: 0 10%;
  text-align: center;
}

 /* PHONE TOGGLE */

#phone-mode-toggle{
  display: none;
  flex-direction: row;
  width: 100%;
  justify-content: center;
}
#phone-mode-toggle input[type=radio]{
  display: none;
}
#phone-mode-toggle input[type=radio] + label{
  text-align: center;
  width: 30%;
  padding: 2% 3%;
  transition: all 0.5s ease-in-out;
  background-color:white;
  color:rgb(0, 143, 191);
  border: solid 1px rgb(0, 143, 191);
}
#phone-mode-toggle input[type=radio]:hover + label{
  background-color:rgb(159, 216, 235);
}
#phone-mode-toggle input[type=radio]:checked + label{
  background-color: rgb(0, 143, 191);
  color: white;
  font-weight: bold;
}
#phone-mode-toggle #edit-label{
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}
#phone-mode-toggle #preview-label{
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
}

@media screen and (max-width:1000px){
  #modal{
    flex-direction: column;
    justify-content: start;
    padding: 0 5%;
  }

  .card{
    display: none;
    width: 90%;
    margin: 0;
  }
  .card.visible{
    display: flex;
  }

  #phone-mode-toggle{
    display: flex;
    margin: 6% 0;
  }
}

@media screen and (max-width:700px){
  #modal{
    padding: 0 2.5%;
  }
  .card{
    width: 95%;
  }
  #creation-modal-toptext{
    font-size: 1.2em;
  }
}

@media screen and (max-width:600px){
  #creation-modal-toptext{
    font-size: 1em;
  }
  #modal .MuiButton-label{
    font-size: 0.9em;
  }
  #creation-modal-container .row#time-row{
    flex-direction: column;
  }

  #preview-card .img-container{
    height: 20vh;
  }
  #preview-card .url-info-container{
    min-height:0;
  }

  #preview-card .next-post-item{
    font-size:0.9em;
  }

}
.alert-btn {
    width: 140px;
    height: 40px;
    border-radius: 40px;
    background-color: #133540;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Poppins";
    font-weight: bold;
    font-size: 18px;
    text-transform: uppercase;
    color: white;
    text-decoration: none;
}


.danger-btn {
    background-color: #F54C4C;
}
.user-img {
  border-radius: 50%;
  width: 10vh;
  height: auto;
}

.skeleton {
  height: 16px;
  margin-top: 6px;
  border-radius: 4px;
  background-color: #d6f3fd;
}

.url-info-container {
  background-color: #f3f6f8;
  width: calc(100% - 40px);
  min-height: 12vh;
  max-height: 20vh;
  align-items: flex-start;
  border-radius: 0 0 20px 20px;
  padding: 20px;
}

.img-container {
  width: 100%;
  height: 40vh;
  background-color: #d6f3fd;
  display: flex;
  justify-content: center;
}

.embed-img {
  height: 100%;
  max-width: 100%;
  object-fit: contain; 
  background-color: white;
}

.description-text {
  margin: 0;
  font-size: 14px;
  word-break: break-all;
}

.clip-1 {
  -webkit-line-clamp: 1;
}
.clip-2 {
  -webkit-line-clamp: 2;
}


#post-content{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.next-post-item{
  margin: 0;
}

#preview-card{
  height: auto;
  max-height: 80vh;
}
.wrapper {
  width: calc(100% - 3vw);
  height: calc(100% - 10vh);
  padding-top: 4vh;
  padding-left: 3vw;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}


.upcoming-text {
  margin: 20px 20px 5px 20px;
  margin: 0;
  padding: 1% 2%;
  letter-spacing: 6px;
  color: white;
  font-size: 22px;
}

#next-posts-container{
  width: 90%;
  display: flex;
  justify-content: center;
}
.next-post {
  background-color: white;
  border-radius: 10px;
  width: 95%;
  padding: 10px;
  max-height: 20vh;
  margin-bottom: 7px;
  cursor: pointer;
  margin-bottom: 20px;

}

.next-post-item {
  font-weight: normal;
  text-overflow: ellipsis;
  word-break: break-all;
}

.clipped {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.span-item {
  font-weight: bold;
}

.empty-img {
  height: auto;
  width: 60%;
  margin-top: 6%;
}

.posts-container{
  margin-top: 10%;
  max-height: 57vh;
  overflow: scroll;
  width: 100%;
}

/* calendar classes */
.calendar {
  background-color: white;
  border-radius: 20px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0 0 20px;
}
.rbc-event {
  max-width: 8vw;
}
.rbc-current-time-indicator {
  background-color: #3da7ca;
}
.rbc-day-slot,
.rbc-day-bg {
  cursor: pointer;
}

.scroll-container{
  width: 44vw;
  height: 30vw;
  margin-top: 50px;
}


@media screen and (max-width: 1000px){
  /* GENERAL  */
  .wrapper {
    width: 90%;
    height: 90%;
    padding: 5%;
    flex-direction: column-reverse;
  }
  .column:not(#home-left-column){
    width: 100%;
  }
  .important-text{
    text-align: center;
  }

  #schedule-container{
    margin-top: 10%;
  }
}

@media screen and (max-width: 600px){

  .posts-container{
    max-height: 40vh;
  }

  /* GENERAL */
  .wrapper {
    width: 100%;
    height: 95%;
    padding: 0%;
    padding-top: 5%;
  }

  .upcoming-text{
    font-size: 1.2em;
  }
  #schedule-title,
  #schedule-subtitle{
    text-align: left;
    margin:0;
    padding: 0 5%;
    padding-top: 5%;
    background-color: white;
    width: 90%;
  }
  #schedule-title{
    font-size: 1.7em;
    box-shadow: 0px -5px 20px 2px rgba(61, 61, 61, 0.26);
  }
  #schedule-subtitle{
    font-size: 1.2em;
    padding-top: 0;
  }

  /* NEXT POSTS  */

  .next-post {
    width: 90%;
    padding: 10px;
    margin-bottom: 7px;
    cursor: pointer;
    margin-bottom: 20px;
  }

  /* CALENDAR  */
  .calendar{
    box-shadow: none;
    border-radius: 0;
  }
  .rbc-toolbar{
    flex-direction: column;
  }
  .rbc-toolbar-label{
    order:3;
    font-weight: bold;
  }
  .rbc-btn-group{
    display: flex;
    margin-bottom: 2%;
    width: 100%;
  }
  .rbc-btn-group button{
    width: 100%;
  }
  .rbc-time-view .rbc-time-gutter span{
    font-size: 0.8em;
  }
  .rbc-label .rbc-time-header-gutter{
    width: 62.3px;
    min-width: 62.3px;
    max-width: 62.3px;
  }

}

