.center {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  background-color: #e3eff3;
}

::-webkit-scrollbar {
  width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 20px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #003040;
  border-radius: 0 20px 20px 0;
}

::-webkit-scrollbar-thumb:hover {
  background: #00698c;
}
